.left_container {
  box-shadow: -1px 6px 15px -11px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -1px 6px 15px -11px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px 6px 15px -11px rgba(0, 0, 0, 0.75);
}

.label_name_container {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  padding: 0.5em;
  border-bottom: 0.5px solid #dddddd;
}

.searchInAdmin {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #f0f2f5;
  padding: 10px 32px 10px 10px;
  border-radius: 8px;
  cursor: text;
}

.searchInAdmin input {
  outline: none;
  border: none;
  background: transparent;
  font-size: 15px;
  font-family: inherit;
}

.label_name_container > span {
  font-size: 15px;
  font-weight: 600;
  color: #333;
  /* font-family: Georgia, 'Times New Roman', Times, serif; */
}

.list_customer_container {
  display: flex;
  flex-direction: column;
  height: 400px;
  overflow-y: auto;
  /* gap: 1em; */
}

.customer_wrapper {
  display: flex;
  align-items: center;
  padding: 0.5em;
  cursor: pointer;
}

.customer_name {
  flex: 1;
}

.numberWrap {
  display: flex;
  align-items: center;
  padding: 0.2em 0.7em;
  border-radius: 50%;
  background: #f5f5f5;
}

/*----Scrollbar----*/

.scrollbar::-webkit-scrollbar {
  width: 8px;
}
.scrollbar::-webkit-scrollbar-thumb {
  background: #aeaeafa1;
  border-radius: 5px;
}
/*----Scrollbar----*/
