.btn_icon_filter_container {
  position: relative;
}

.filter_wrapperr {
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 300px;
  height: 210px;
  bottom: 0;
  right: 0;
  /* transform: translateY(180px); */
  padding: 8px;
  border-radius: 12px;
  background: rgba(51, 141, 237, 0.595);
  color: #000;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Responsive */
@media (max-width: 600px) {
  .header_container {
    display: flex;
    flex-direction: column !important;
    justify-content: start;
    align-items: flex-start !important;
    padding-bottom: 0 !important;
  }
  .search_input_container {
    padding: 0 !important;
  }
  .search_input_container input {
    border-radius: 10px !important;
    outline: none !important;
  }
  .search_input_container input:focus {
    outline: none !important;
    border-color: #fff;
  }

  .search_input_button_container button {
    height: 10px;
    width: 54%;
  }

  .search_input_button_tele_container {
    padding-bottom: 1em;
  }

  .text_list_batch {
    font-size: 8px;
  }
  .dialog_progress_container {
    display: flex;
    flex-direction: column !important;
  }

  .left_container {
    width: 100% !important;
    margin: 0 !important;
  }

  .list_customer_container {
    height: 250px !important;
  }
}
/* Responsive */
