.card_report_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 0 1em;
}

.card_report_container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 0 1em 1em 1em;
}

.card_report_wrap_1 {
  position: relative;
  width: 23%;
  background-image: none;
  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  overflow: hidden;
  padding-top: 40px;
  padding-bottom: 40px;
  text-align: center;
  border-radius: 12px;
  color: rgb(6, 27, 100);
  background-color: rgb(209, 233, 252);
}

