.icon_report {
  width: 64px;
  height: 64px;
  display: flex;
  margin: auto auto 24px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  color: rgb(16, 57, 150);
  background-image: linear-gradient(
    135deg,
    rgba(16, 57, 150, 0) 0%,
    rgba(16, 57, 150, 0.24) 100%
  );
}
