
@media (min-width: 1024px) {
  /* Tambahkan gaya CSS yang sesuai untuk laptop di sini */

  /* Gaya untuk elemen .apaansi */
  .apaansi
  .enemy {
    /* Contoh: Mengatur lebar dan tinggi elemen pada laptop */
    width: 1980px;
    height: 1080px;
  }

  /* Gaya untuk elemen .informasikendaraan */
  .informasikendaraan {
    /* Contoh: Mengatur lebar dan tinggi elemen pada laptop */
    width: 2px;
    height: 2px;
  }

  /* Tambahkan aturan CSS lainnya untuk tampilan laptop di sini */
}

/* Media query untuk peta (ukuran layar selain dari 1024px) */
@media (max-width: 1023px) {
  /* Tambahkan gaya CSS khusus untuk peta di sini */
  .apaansi {
    /* Aturan CSS untuk elemen .apaansi pada layar yang lebih kecil dari 1024px */
    width: 100%;
    height: 100%;
  }
  .informasikendaraan {
    /* Aturan CSS untuk elemen .informasikendaraan pada layar yang lebih kecil dari 1024px */
    width: 100%;
    height: 100%;
    /* Tambahkan gaya lain yang Anda butuhkan untuk peta di sini */
  }
}

/* Media query untuk ukuran layar 820 x 1180 */
@media (max-width: 820px) {
  /* Aturan CSS khusus untuk ukuran layar 820 x 1180 */
  .apaansi {
    width: 20%; /* Lebar elemen 100% dari lebar layar */
    height: auto; /* Tinggi elemen menyesuaikan dengan kontennya */
    /* Tambahkan gaya lain yang Anda butuhkan untuk peta di sini */
  }

  .customGridItem {
    width: 50%; /* Lebar elemen 100% dari lebar layar */
  }

  .informasi {
    background: white;
    padding: 10px;
    width: auto;
    height: auto;
    display: flex;
    gap: 5px;
    flex-direction: column;
    margin: 40px 0; /* Sesuaikan margin sesuai kebutuhan Anda */
    font-size: 13px; /* Ubah ukuran font sesuai kebutuhan Anda */
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
    justify-content: space-around;
  }


  .putadashbord{
    width: 100%; /* Lebar elemen 100% dari lebar layar */
    margin: 10px 0; /* Sesuaikan margin sesuai kebutuhan Anda */
    font-size: 13px; /* Ubah ukuran font sesuai kebutuhan Anda */
  }

  .mup{
    width: 50%; /* Lebar elemen 100% dari lebar layar */
    height: auto; /* Tinggi elemen menyesuaikan dengan kontennya */
    margin: 10px 0; /* Sesuaikan margin sesuai kebutuhan Anda */
    font-size: 13px; /* Ubah ukuran font sesuai kebutuhan Anda */
  }

  /* Tambahkan aturan CSS lainnya yang sesuai dengan ukuran layar 820 x 1180 di sini */
}

.address {
  max-height: 50px; /* Sesuaikan tinggi maksimum sesuai kebutuhan Anda */
  overflow: hidden;
  font-size: 25px;
  text-overflow: ellipsis; /* Menambahkan titik-titik elipsis jika alamat terlalu panjang */
  white-space: nowrap; /* Menghindari teks berjalan ke bawah */
}

.carDetailKey {
  font-size: 12px;
  color: #00000080;
}


.carDetailValue {
  font-size: 14px;
  color: #000000;
}

.carDetailValue13 {
  font-size: 13px;
  color: #000000;
}

.carDetailContainer {
  border: 1px solid #00000060;
  padding: 10px;
  border-radius: 10px;
  height: 50vh;
}

.pinPointDashboard {
  font-size: 25px !important;
}